*
{
    box-sizing:border-box;
    padding:0;
    margin: 0;
    font-family: "Josefin Sans","Arial Narrow",Arial, Sans-serif;
}
.main_div
{
    width:100%;
    height:100vh;
    background: #7bed9f;
    display:flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.center_div
{
    width: 50%;
    height:70vh;
    background-color: #f4f4f4;
    box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
    border-radius:15px;
}
h1{
    color: rgba(0,0,0,0.87);
    font-size: 50px;
    margin-bottom: 30px;
    min-width: 64x;
    box-sizing: border-box;
    font-family: "comfortaa",cursive;
    font-weight:500;
    border-radius: 4px;
    letter-spacing: 0.02857em;
    text-transform: uppercase;
    text-shadow:5px 5px 5px rgba(0,0,0,0.3);
}
.entry{
    text-align:center;
    height:30px;
    border:none;
    background:transparent;
    font-size:20px;
    font-weight:20px;
    width: 60%;
    border-bottom: 2px solid #8566aa;
    outline:none;
    margin-right:20px
}
.newBtn
{
    width: 50px;
    height: 55px;
    background-color:#16a085 !important;
    color: white impo !important;
    box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
    border-radius: 50% importat !important;
}
