*
{
    box-sizing:border-box;
    padding:0;
    margin: 0;
    font-family: "Josefin Sans","Arial Narrow",Arial, Sans-serif;
}
.main_div
{
    width:100%;
    height:100vh;
    background: #7bed9f;
    display:flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.center_div
{
    width: 15%;
    height:25vh;
    background-color: #f4f4f4;
    box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
    border-radius:15px;
}
input{
    text-align:center;
    height:30px;
    border:none;
    background:transparent;
    font-size:20px;
    font-weight:20px;
    width: 60%;
    border-bottom: 2px solid #8566aa;
    outline:none;
    margin-right:20px
}
.setting
{
    margin-top: 120px;
}
li{
    font-weight: bold;
    font-size: medium;
    font-style: oblique;
}